
import "@/sass/globals.scss";

import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { SupportedPostType } from "./types";
import Notifications from "@/components/Notifications/Notifications.vue";
import ManageTemplates from "@/subapps/manage-templates/ManageTemplates.vue";
import ManageGlobalBlocks from "@/subapps/manage-templates/ManageGlobalBlocks.vue";
import { initializeOrReinitializeCombokeys } from "@/lib/keyboard-shortcuts/keyboard-shortcuts";
import { configStore } from "@/store/store-accessor";

@Component({
  components: {
    ManageGlobalBlocks,
    ManageTemplates,
    Notifications
  }
})
export default class ManageBreakdancePostTypes extends Vue {
  @Prop()
  postType!: SupportedPostType;

  addingTemplate = false;

  mounted() {
    initializeOrReinitializeCombokeys(document);

    configStore.setSubscriptionMode(window.Breakdance.subscriptionMode);
  }

  get isBlockPostType() {
    return (
      this.postType === "breakdance_block" ||
      this.postType === "breakdance_acf_block"
    );
  }
}
