
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import {
  deleteTemplateFromWordPress,
  restoreTemplateFromWordPress
} from "./lib/api";
import Icon from "@/components/UI/Icon.vue";
import { GlobalBlock } from "@/types/global-blocks";
import defensiveCopy from "@/util/defensive-copy";

@Component({
  components: { Icon }
})
export default class BlockRow extends Vue {
  @Prop()
  value!: GlobalBlock;

  openEditBlockModal() {
    this.$emit("input", this.value);
  }

  duplicateBlock() {
    const duplicatedBlock = defensiveCopy(this.value);

    // tell WP to save as a new block
    duplicatedBlock.id = -1;
    duplicatedBlock.label = `Copy of "${duplicatedBlock.label}"`;

    this.$emit("save", duplicatedBlock);
  }

  deleteInProgress = false;

  async deleteBlock() {
    this.deleteInProgress = true;

    const response = await deleteTemplateFromWordPress(
      this.value.label,
      this.value.id,
      this.value.status
    );

    if (response) {
      this.$emit(
        this.value.status === "publish" ? "trash" : "delete",
        response
      );
    }

    this.deleteInProgress = false;
  }

  restoreInProgress = false;

  async restoreBlock() {
    this.restoreInProgress = true;

    const response = await restoreTemplateFromWordPress(
      this.value.label,
      this.value.id
    );

    if (response) this.$emit("restore", response);

    this.restoreInProgress = false;
  }

  get mode() {
    return this.value.status;
  }

  highlightValue(e: Event) {
    // @ts-ignore
    const selectIt = () => e.target.select();

    setTimeout(() => {
      selectIt();
    }, 100);
  }
}
