
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import {
  TemplateParents,
  TemplateTypeCategories,
  TemplateWithValidSettings
} from "./types";
import ConditionsPreviewTooltip from "@/components/Reusable/CustomInputs/ConditionInput/ConditionPreviewTooltip.vue";
import {
  deleteTemplateFromWordPress,
  restoreTemplateFromWordPress
} from "./lib/api";
import Icon from "@/components/UI/Icon.vue";
import { QUICK_ADD_TEMPLATES } from "@/subapps/manage-templates/lib/quick-add-templates";
import ElementBadge from "@/components/ElementPropertiesPanel/ElementBadge.vue";
import { Trigger } from "@/types/conditions";
import ToggleInput from "@/components/Reusable/CustomInputs/ToggleInput.vue";
import defensiveCopy from "@/util/defensive-copy";

@Component({
  components: {
    ToggleInput,
    Icon,
    ConditionsPreviewTooltip,
    ElementBadge
  }
})
export default class TemplateRow extends Vue {
  @Prop()
  value!: TemplateWithValidSettings;

  @Prop()
  templateTypeCategories!: TemplateTypeCategories;

  @Prop()
  templateParents!: TemplateParents;

  openEditTemplateModal() {
    if (this.mode === "trash") {
      return;
    }

    this.$emit("input", "edit", this.value);
  }

  get mode() {
    return this.value.status;
  }

  get appliesToLabel() {
    const templateType = this.value.settings.type;
    let templateTypeLabel = "";

    this.templateTypeCategories.some(templateTypeCategory => {
      const currentTemplateType = templateTypeCategory.types.find(
        type => type.slug === templateType
      );

      if (currentTemplateType) {
        templateTypeLabel = currentTemplateType.label;

        return true;
      }

      return false;
    });

    return templateTypeLabel;
  }

  deleteInProgress = false;

  async deleteTemplate() {
    this.deleteInProgress = true;

    const response = await deleteTemplateFromWordPress(
      this.value.title,
      this.value.id,
      this.value.status
    );

    if (response) {
      this.$emit(
        this.value.status === "publish" ? "trash" : "delete",
        response
      );
    }

    this.deleteInProgress = false;
  }

  restoreInProgress = false;

  async restoreTemplate() {
    this.restoreInProgress = true;

    const response = await restoreTemplateFromWordPress(
      this.value.title,
      this.value.id
    );

    if (response) this.$emit("restore", response);

    this.restoreInProgress = false;
  }

  duplicateTemplate() {
    const duplicatedTemplate = defensiveCopy(this.value);

    duplicatedTemplate.title = `Copy of "${duplicatedTemplate.title}"`;
    duplicatedTemplate.settings.disabled = true;

    this.$emit("duplicate", duplicatedTemplate);
  }

  get matchingQuickAddTemplate() {
    return QUICK_ADD_TEMPLATES.find(
      quickAddTemplate =>
        quickAddTemplate.settings.type === this.value.settings.type
    );
  }

  triggerLabels(triggers: Trigger[]) {
    return triggers
      .map(trigger => {
        return trigger.slug;
      })
      .join(", ");
  }

  updateDisabled(enabled: boolean) {
    const newTemplate = defensiveCopy(this.value);

    newTemplate.settings.disabled = !enabled;

    this.$emit("update", newTemplate);
  }
}
