
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Dropdown from "@/components/Reusable/Dropdown/Dropdown.vue";
import { QuickAddTemplate, Template, TemplateWithValidSettings } from "./types";
import { saveTemplateToWordPress } from "./lib/api";
import { pipe } from "fp-ts/lib/function";
import { fold } from "fp-ts/lib/Either";
import NotificationLogger from "@/lib/notifications/notifications";
import { QUICK_ADD_TEMPLATES } from "@/subapps/manage-templates/lib/quick-add-templates";
import { checkIfTemplateSettingsAlreadyExistsAndConfirmToContinue } from "@/subapps/manage-templates/lib/utils";
import ElementBadge from "@/components/ElementPropertiesPanel/ElementBadge.vue";

@Component({
  components: { Dropdown, ElementBadge }
})
export default class AddTemplateDropdown extends Vue {
  @Prop()
  templateName!: string;

  @Prop()
  isWooCommerceActive!: boolean;

  @Prop()
  templates!: Template[];

  open = false;
  quickAddInProgress = false;

  get quickAddTemplates() {
    return this.isWooCommerceActive
      ? QUICK_ADD_TEMPLATES
      : QUICK_ADD_TEMPLATES.filter(
          template => template.category.toLowerCase() !== "woocommerce"
        );
  }

  async quickAdd(quickAddTemplate: QuickAddTemplate) {
    const templateToAdd: TemplateWithValidSettings = {
      // -1 = new post in wp
      id: -1,
      title: quickAddTemplate.name,
      editInBreakdanceLink: "",
      postType: "breakdance_template",
      settings: quickAddTemplate.settings,
      status: "publish"
    };

    if (
      !checkIfTemplateSettingsAlreadyExistsAndConfirmToContinue(
        this.templates,
        quickAddTemplate.settings,
        "template"
      )
    ) {
      return;
    }

    if (quickAddTemplate.openDialog) {
      this.$emit("modalWithSettings", templateToAdd);

      return;
    }

    this.quickAddInProgress = true;

    pipe(
      await saveTemplateToWordPress(templateToAdd),
      fold(
        () => {
          NotificationLogger.log.message(
            `The new ${this.templateName} couldn't be saved`
          );
        },
        savedTemplate => {
          this.$emit("input", savedTemplate);
        }
      )
    );

    this.quickAddInProgress = false;
  }
}
