
import "@/sass/globals.scss";

import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";
import { pipe } from "fp-ts/lib/function";
import {
  TemplatesManagementData,
  SupportedPostType,
  Template,
  TemplateParents,
  TemplateSettings,
  TemplateSettingsV,
  TemplateWithValidSettings,
  EmptyStateV
} from "./types";
import { fold } from "fp-ts/lib/Either";

import TemplateRow from "./TemplateRow.vue";
import AddTemplateDropdown from "./AddTemplateDropdown.vue";
import {
  duplicateTemplateToWordPress,
  emptyTrashForPostType,
  getAllTemplatesAndNeededDataFromWordPress,
  saveTemplateToWordPress
} from "./lib/api";
import NotificationLogger from "@/lib/notifications/notifications";
import { configStore } from "@/store/store-accessor";
import EditTemplateModal from "@/subapps/manage-templates/EditTemplateModal.vue";
import Notifications from "@/components/Notifications/Notifications.vue";
import Loading from "@/components/Reusable/Loading.vue";
import {
  changeTemplateStatus,
  deleteAllTrash,
  deleteTemplate,
  saveNewTemplate
} from "@/subapps/manage-templates/lib/utils";
import SearchInput from "@/components/Reusable/CustomInputs/SearchInput.vue";
import Icon from "@/components/UI/Icon.vue";
import EmptyState from "@/subapps/manage-templates/EmptyState.vue";
import { keyboardShortcuts } from "@/lib/keyboard-shortcuts/keyboard-shortcuts";
import { listenForNonceUpdates } from "@/util/nonce-broadcast";

@Component({
  components: {
    Icon,
    SearchInput,
    Loading,
    Notifications,
    EditTemplateModal,
    AddTemplateDropdown,
    TemplateRow,
    EmptyState
  }
})
export default class ManageBreakdancePostTypes extends Vue {
  @Prop()
  postType!: SupportedPostType;

  modalData: { type: "edit" | "add"; data: Template } | false = false;

  skipWarning = false;

  templates: Template[] = [];
  templatesManagementData: TemplatesManagementData = {
    conditions: [],
    dynamicFields: [],
    templateTypeCategories: [],
    popupTriggers: null
  };

  isWooCommerceActive = false;
  search = "";
  showTrash = false;

  newTemplateAdded = false;

  hasPermissionToManageFallbacks = false;

  @Watch("newTemplateAdded")
  onTemplateAdded() {
    setTimeout(() => (this.newTemplateAdded = false), 1000);
  }

  get templatesForUi() {
    return this.hasPermissionToManageFallbacks
      ? this.templatesByStatus
      : this.templatesByStatus.filter(t => !t.settings?.fallback);
  }

  get filteredTemplatesBySearch() {
    return this.search
      ? this.templatesForUi.filter(template => {
          return (
            template.title.toLowerCase().includes(this.search.toLowerCase()) ||
            template.settings?.type?.includes(this.search.toLowerCase())
          );
        })
      : this.templatesForUi;
  }

  get templatesByStatus() {
    return this.templates.filter(template =>
      this.showTrash
        ? template.status === "trash"
        : template.status === "publish"
    );
  }

  get blankTemplateWithValidSettings(): TemplateWithValidSettings {
    return {
      id: -1,
      title: "",
      postType: this.postType,
      editInBreakdanceLink: "",
      settings: {},
      status: "publish"
    };
  }

  get trashLength() {
    return this.templates.filter(template => template.status === "trash")
      .length;
  }

  async duplicateTemplate(newTemplateWithOldId: TemplateWithValidSettings) {
    pipe(
      await duplicateTemplateToWordPress(newTemplateWithOldId),
      fold(
        () => {
          NotificationLogger.log.message(
            `Duplicating template "${newTemplateWithOldId.id}" failed`
          );
        },
        newTemplate => {
          this.saveTemplate(newTemplate);
        }
      )
    );
  }

  async updateTemplate(updatedTemplate: TemplateWithValidSettings) {
    pipe(
      await saveTemplateToWordPress(updatedTemplate),
      fold(
        () => {
          NotificationLogger.log.message(
            `Update for template "${updatedTemplate.id}" failed`
          );
        },
        newTemplate => {
          this.templates = saveNewTemplate(newTemplate, this.templates);
        }
      )
    );
  }

  saveTemplate(newTemplate: Template) {
    this.templates = saveNewTemplate(newTemplate, this.templates);

    this.newTemplateAdded = true;
  }

  deleteTemplate(idToDelete: number) {
    this.templates = deleteTemplate(idToDelete, this.templates);
  }

  trashTemplate(idToDelete: number) {
    this.templates = changeTemplateStatus(idToDelete, this.templates, "trash");
  }

  restoreTemplate(idToRestore: number) {
    this.templates = changeTemplateStatus(
      idToRestore,
      this.templates,
      "publish"
    );
  }

  emptyingTrash = false;
  async emptyTrash() {
    this.emptyingTrash = true;

    if (await emptyTrashForPostType(this.postType)) {
      this.templates = deleteAllTrash(this.templates);
      this.showTrash = false;
    }

    this.emptyingTrash = false;
  }

  get templateParentsWithoutTrashed(): TemplateParents {
    return this.templates
      .filter(template => template.status !== "trash")
      .map(template => ({
        text: template.title,
        value: template.id
      }));
  }

  get templateName() {
    if (this.postType === "breakdance_template") return "Template";
    if (this.postType === "breakdance_footer") return "Footer";
    if (this.postType === "breakdance_header") return "Header";
    if (this.postType === "breakdance_popup") return "Popup";

    return "Unsupported template";
  }

  get emptyStateData(): EmptyStateV {
    return {
      itemsByStatus: this.templatesByStatus.length,
      filteredItemsBySearch: this.filteredTemplatesBySearch.length,
      templateName: this.templateName,
      showTrash: this.showTrash
    };
  }

  get searchInputPlaceholder() {
    const templateName = this.templateName.toLowerCase();
    return `Search for ${templateName}s`;
  }

  isLoadingData = false;

  broadcastChannel: BroadcastChannel | null = null;
  beforeMount() {
    this.broadcastChannel = listenForNonceUpdates();
  }

  async mounted() {
    keyboardShortcuts.register("b r e a k d a n c e", () => {
      this.hasPermissionToManageFallbacks =
        !this.hasPermissionToManageFallbacks;
    });

    this.isLoadingData = true;

    pipe(
      await getAllTemplatesAndNeededDataFromWordPress(this.postType),
      fold(
        () => {
          NotificationLogger.log.message("Couldn't load templates data");
        },
        data => {
          this.templates = data.templates;
          this.templatesManagementData = data.templatesManagementData;
          this.isWooCommerceActive = data.isWooCommerceActive;
          configStore.setDynamicFields(
            data.templatesManagementData.dynamicFields
          );
          // Necessary for ConditionPreviewRule to display the condition's label instead of the rule slug
          configStore.setConditions(data.templatesManagementData.conditions);
          configStore.setEnvironment(data.environment);
        }
      )
    );

    this.isLoadingData = false;
  }

  beforeDestroy() {
    keyboardShortcuts.unbind("b r e a k d a n c e");
    if (this.broadcastChannel) {
      this.broadcastChannel.close();
    }
  }

  getTemplateWithValidSettings(template: Template): TemplateWithValidSettings {
    return {
      ...template,
      settings: pipe(
        template.settings,
        TemplateSettingsV.decode,
        fold(
          () => {
            return DEFAULT_TEMPLATE_SETTINGS;
          },
          (templateSettings: TemplateSettings) => {
            return templateSettings;
          }
        )
      )
    };
  }

  handleTemplateCardModal(type: "edit" | "add", newTemplate: Template) {
    this.modalData = { type: type, data: newTemplate };
  }
}

const DEFAULT_TEMPLATE_SETTINGS: TemplateSettings = {
  type: "none",
  ruleGroups: []
};
