
import "@/sass/globals.scss";

import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import { pipe } from "fp-ts/lib/function";
import { fold } from "fp-ts/lib/Either";
import NotificationLogger from "@/lib/notifications/notifications";
import EditGlobalBlockModal from "@/subapps/manage-templates/EditGlobalBlockModal.vue";
import BlockRow from "@/subapps/manage-templates/BlockRow.vue";
import Loading from "@/components/Reusable/Loading.vue";
import {
  saveNewTemplate,
  changeTemplateStatus,
  deleteAllTrash,
  deleteTemplate
} from "@/subapps/manage-templates/lib/utils";
import SearchInput from "@/components/Reusable/CustomInputs/SearchInput.vue";
import Icon from "@/components/UI/Icon.vue";
import {
  emptyTrashForPostType,
  getAllGlobalBlocksFromWordPress
} from "@/subapps/manage-templates/lib/api";
import { GlobalBlock } from "@/types/global-blocks";
import { EmptyStateV } from "./types";
import EmptyState from "@/subapps/manage-templates/EmptyState.vue";
import { listenForNonceUpdates } from "@/util/nonce-broadcast";
import { saveGlobalBlockToWordPress } from "@/lib/global-blocks/global-blocks";

@Component({
  components: {
    Icon,
    SearchInput,
    Loading,
    BlockRow,
    EditGlobalBlockModal,
    EmptyState
  }
})
export default class ManageBreakdancePostTypes extends Vue {
  modalData: { type: "edit" | "add"; data: GlobalBlock } | false = false;

  globalBlocks: GlobalBlock[] = [];

  showTrash = false;

  newBlockAdded = false;

  @Watch("newBlockAdded")
  onBlockAdded() {
    setTimeout(() => (this.newBlockAdded = false), 1000);
  }

  get blankGlobalBlock(): GlobalBlock {
    return {
      label: "",
      id: -1,
      tree: false,
      editInBreakdanceLink: "",
      status: "publish"
    };
  }

  async saveBlockToWordPress(newBlock: GlobalBlock) {
    pipe(
      await saveGlobalBlockToWordPress(newBlock),
      fold(
        () => {
          NotificationLogger.log.message("Global block was not saved");
        },
        savedBlock => {
          this.saveBlock(savedBlock);
        }
      )
    );
  }

  saveBlock(newBlock: GlobalBlock) {
    this.globalBlocks = saveNewTemplate(newBlock, this.globalBlocks);
    this.newBlockAdded = true;
  }

  deleteBlock(idToDelete: number) {
    this.globalBlocks = deleteTemplate(idToDelete, this.globalBlocks);
  }

  trashBlock(idToDelete: number) {
    this.globalBlocks = changeTemplateStatus(
      idToDelete,
      this.globalBlocks,
      "trash"
    );
  }

  restoreBlock(idToRestore: number) {
    this.globalBlocks = changeTemplateStatus(
      idToRestore,
      this.globalBlocks,
      "publish"
    );
  }

  emptyingTrash = false;
  async emptyTrash() {
    this.emptyingTrash = true;

    if (await emptyTrashForPostType("breakdance_block")) {
      this.globalBlocks = deleteAllTrash(this.globalBlocks);
      this.showTrash = false;
    }

    this.emptyingTrash = false;
  }

  isLoadingData = false;
  search = "";

  get emptyStateData(): EmptyStateV {
    return {
      itemsByStatus: this.globalBlocksByStatus.length,
      filteredItemsBySearch: this.filteredBlocksBySearch.length,
      templateName: "Global Block",
      showTrash: this.showTrash
    };
  }

  get filteredBlocksBySearch() {
    return this.globalBlocksByStatus.filter(block => {
      return block.label.toLowerCase().includes(this.search.toLowerCase());
    });
  }

  get globalBlocksByStatus() {
    return this.globalBlocks.filter(block =>
      this.showTrash ? block.status === "trash" : block.status === "publish"
    );
  }

  get trashLength() {
    return this.globalBlocks.filter(block => block.status === "trash").length;
  }

  broadcastChannel: BroadcastChannel | null = null;
  beforeMount() {
    this.broadcastChannel = listenForNonceUpdates();
  }

  async mounted() {
    this.isLoadingData = true;

    pipe(
      await getAllGlobalBlocksFromWordPress(),
      fold(
        () => {
          NotificationLogger.log.message("Couldn't load Global Blocks data");
        },
        blocks => {
          this.globalBlocks = blocks;
        }
      )
    );

    this.isLoadingData = false;
  }

  beforeDestroy() {
    if (this.broadcastChannel) {
      this.broadcastChannel.close();
    }
  }
}
